import { Component, createContext } from "react";
import SubmissionType, { OrganiserType, TaskType } from "./types";
import axios from "../utils/axiosurl";

import { withRouter, RouteComponentProps } from "react-router";
import { showUnsuccessfulAlert } from "../services/alertService";

type StateType = {
	loading: boolean;
	tasks: TaskType[];
	category_title: string;
	organisers: OrganiserType[];
	addSubmission: (submission: SubmissionType, uuid: string) => void;
};

interface PropType
	extends RouteComponentProps<{ domainID: string; taskID: string }> {}

export const TaskContext = createContext<StateType>({
	loading: true,
	category_title: "",
	tasks: [],
	organisers: [],
	addSubmission: (submission: SubmissionType, uuid: string) => {},
});

class TaskProvider extends Component<PropType, StateType> {
	constructor(props: any) {
		super(props);
		this.state = {
			loading: true,
			category_title: "",
			tasks: [],
			organisers: [],
			addSubmission: this.addSubmission,
		};
	}

	componentDidMount() {
		//console.log("Fetching tasks");
		type DataType = {
			status: "successful" | "unsuccessful";
			tasks: TaskType[];
			category_title: string;
			organisers: OrganiserType[];
		};
		axios
			.get("/course/get_tasks/" + this.props.match.params.domainID)
			.then(({ data }: { data: DataType }) => {
				if (data.status === "successful") {
					this.setState({
						loading: false,
						tasks: data.tasks,
						category_title: data.category_title,
						organisers: data.organisers,
					});
				} else {
					this.props.history.replace("/not-found");
				}
			})
			.catch(err => showUnsuccessfulAlert(err));
	}

	addSubmission = (submission: SubmissionType, uuid: string) => {
		for (let i = 0; i < this.state.tasks.length; i++) {
			if (this.state.tasks[i].uuid === uuid) {
				this.setState(() => {
					let task = this.state.tasks[i];
					task.submission = submission;
					return {
						tasks: this.state.tasks.map((task_map, id) =>
							id === i ? task : task_map
						),
					};
				});
			}
		}
	};

	render() {
		return (
			<TaskContext.Provider value={this.state}>
				{this.props.children}
			</TaskContext.Provider>
		);
	}
}

export default withRouter(TaskProvider);

export const TaskConsumer = TaskContext.Consumer;
